.s-support-form {
    overflow: hidden;

    &__columns {
        display: flex;
        margin: 0 -50px;

        @media (max-width: 1499px) {
            margin: 0 -25px;
        }

        @media (max-width: 1199px) {
            flex-wrap: wrap;
            margin: 0 -15px;
        }

        @media (max-width: 600px) {
            flex-wrap: wrap;
            margin: 0;
        }
    }

    &__column {
        display: flex;
        flex-direction: column;
        flex: 1;
        max-width: 500px;
        margin: 0 50px;

        @media (max-width: 1499px) {
            margin: 0 25px;
        }

        @media (max-width: 1199px) {
            width: calc(50% - 30px);
            margin: 0 15px;
            max-width: 100%;
        }

        @media (max-width: 600px) {
            width: 100%;
            margin: 0;
            margin-bottom: 20px;

            &:last-child {
                margin-bottom: 0;
            }
        }
    }

    &__info {
        @media (max-width: 1199px) {
            width: 100%;
            max-width: 100%;
            order: 9;
            margin: 0;
            margin-top: 98px;

            .form__title {
                display: none;
            }

            .s-support-form__bottom-item {
                margin: 0;
            }
        }

        @media (max-width: 1199px) {
            margin-top: 55px;
        }

        @media (max-width: 991px) {
            margin-top: 50px;
        }

        @media (max-width: 600px) {
            margin-top: 44px;
        }
    }

    &__contacts {
        display: flex;
        flex-direction: column;
        flex: 1;
        max-width: 425px;
        min-width: 425px;
        margin-left: auto;

        @media (max-width: 1199px) {
            flex-direction: row;
            width: 100%;
            max-width: 100%;
            min-width: 0;
            margin: 0;
        }

        @media (max-width: 600px) {
            flex-direction: column;
        }

        &-title {
            font-size: 40px;
            line-height: 120%;

            @media (max-width: 1699px) {
                font-size: 38px;
            }

            @media (max-width: 991px) {
                width: 100%;
                font-size: 19px;
                margin-bottom: 7px;
            }
        }

        &-left {
            @media (max-width: 1199px) {
                width: calc(50% - 30px);
                margin: 0 15px;
            }

            @media (max-width: 600px) {
                width: 100%;
                margin: 0;
                margin-bottom: 12px;
            }
        }

        &-phone {
            display: block;
            margin-bottom: 30px;

            @media (max-width: 1199px) {
                margin-bottom: 0;
                margin-right: 56px;
            }

            @media (max-width: 991px) {
                margin-right: 37px;
            }
        }

        &-phone, &-email {

            @media (max-width: 1199px) {
                order: 1;
            }

            label {
                display: block;
                font-size: 12px;
                line-height: .05;
                color: #333333;
            }

            span {
                font-size: 40px;
                line-height: 120%;
                white-space: nowrap;
                font-size: 40px;
                color: #656669;

                @media (max-width: 1699px) {
                    font-size: 38px;
                }

                @media (max-width: 1199px) {
                    font-size: 30px;
                }

                @media (max-width: 991px) {
                    font-size: 25px;
                }
            }
        }

        &-links {
            margin-top: auto;

            @media (max-width: 1199px) {
                display: flex;
                flex-wrap: wrap;
                width: calc(50% - 30px);
                margin: 0 15px;

                .s-support-form__contacts-message {
                    order: 0;
                    font-size: 17px;
                    margin-bottom: 16px;
                }
            }

            @media (max-width: 600px) {
                width: 100%;
                margin: 0;
            }
        }

        &-message {
            font-size: 25px;
            line-height: 120%;
            color: $red;

            @media (max-width: 991px) {
                font-size: 15px;
            }
        }
    }

    &__inputs {
        display: flex;
        width: calc(100% - 425px);

        @media (max-width: 1199px) {
            width: 100%;
        }

        @media (max-width: 600px) {
            flex-direction: column;
        }
    }

    &__mobile {
        @media (max-width: 1199px) {
            order: 2;
            width: 100%;
            margin-top: 64px;
        }

        @media (max-width: 991px) {
            margin-top: 50px;
        }

        @media (max-width: 600px) {
            margin-top: 10px;
        }
    }

    &__bottom {
        display: flex;
        justify-content: space-between;
        margin: 0 -50px;
        margin-top: 105px;

        @media (max-width: 1199px) {
            margin: 0 15px;
        }

        &-item {
            max-width: 425px;
            min-width: 425px;
            margin: 0 50px;

            @media (max-width: 1199px) {
                max-width: fit-content;
                min-width: 0;
                margin: 0 15px;
            }
        }

        &-left {
            display: flex;
            flex: 1;
            width: calc(100% - 425px);

            @media (max-width: 1199px) {
                width: 100%;
            }

            @media (max-width: 600px) {
                flex-direction: column;
            }

            .s-support-form {
                &__bottom {
                    &-item {
                        display: flex;
                        flex-direction: column;
                        flex: 1;
                        max-width: 500px;
                        min-width: 0;
                        margin: 0 50px;

                        @media (max-width: 1199px) {
                            margin: 0 15px;
                        }

                        @media (max-width: 600px) {
                            margin: 0;
                        }
                    }
                }
            }
        }
    }

    .form {
        &__submit {
            max-width: 100%;
            font-size: 15px;
            line-height: 1;
            color: #fff;
            border-radius: 5px;
            background: #333333;
            padding: 10px 0;
        }

        &__title {
            min-height: 92px;
            font-size: 40px;
            line-height: 1;
            margin-bottom: 43px;

            @media (max-width: 1699px) {
                font-size: 38px;
            }

            @media (max-width: 1199px) {
                min-height: 0;
                font-size: 30px;
                margin-bottom: 41px;
            }

            @media (max-width: 991px) {
                font-size: 25px;
                margin-bottom: 45px;
            }

            @media (max-width: 600px) {
                font-size: 23px;
                margin-bottom: 22px;
            }
        }

        &__inputs {
            display: flex;
            flex-direction: column;
            flex: 1;
        }

        &__input {
            margin-bottom: 43px;

                @media (max-width: 1199px) {
                    margin-bottom: 38px;
                }

                @media (max-width: 991px) {
                    margin-bottom: 30px;
                }

                @media (max-width: 600px) {
                    margin-bottom: 34px;
                }

            &:last-child {
                margin-bottom: 0;
            }

            input {
                min-height: 48px;
            }

            label {
                margin-bottom: 6px;
            }
        }

        &__textarea {
            display: flex;
            flex-direction: column;
            flex: 1 1 auto;
            margin-bottom: 0;

            @media (max-width: 600px) {
                min-height: 130px;
            }

            textarea {
                height: 100%;
            }

            label {
                margin-bottom: 6px;
            }
        }

        &__checkbox {
            align-items: center;

            @media (max-width: 600px) {
                margin-bottom: 20px;
            }

            &-text {
                font-size: 12px;

                @media (max-width: 600px) {
                    font-size: 10px;
                }
            }
        }

        &__radios {
            display: flex;
            align-items: center;
            margin-bottom: 115px;

            @media (max-width: 1699px) {
                margin-bottom: 110px;
            }

            @media (max-width: 1199px) {
                justify-content: space-between;
                margin-bottom: 100px;
            }

            @media (max-width: 991px) {
                margin-bottom: 76px;
            }

            @media (max-width: 600px) {
                flex-direction: column;
                align-items: flex-start;
                margin-bottom: 100px;
            }
        }

        &__radio {
            position: relative;
            margin-right: 40px;

            @media (max-width: 1699px) {
                margin-right: 60px;
            }

            @media (max-width: 1199px) {
                margin-right: 0;
            }

            @media (max-width: 600px) {
                margin-bottom: 37px;
            }

            &:last-child {
                margin-right: 0;
            }

            &-input {
                position: absolute;
                top: 0;
                left: 0;
                z-index: 1;
                width: 100%;
                height: 100%;
                opacity: 0;
                cursor: pointer;

                &:checked + .form__radio-text {
                    color: #fff;
                }
            }

            &-text {
                @include tr('all');
                font-size: 30px;
                line-height: 1;
                white-space: nowrap;
                color: #4E5056;

                @media (max-width: 1699px) {
                    font-size: 28px;
                }

                @media (max-width: 1199px) {
                    font-size: 25px;
                }

                @media (max-width: 991px) {
                    font-size: 21px;
                }
            }

            &:hover {
                .form__radio-text {
                    color: #fff;
                }
            }
        }
    }

    &__title {
        font-size: 50px;
        line-height: 120%;
        color: #4E5056;
        letter-spacing: -0.03em;
        text-align: center;
        margin-bottom: 80px;

        @media (max-width: 1199px) {
            font-size: 40px;
            text-align: left;
            margin-bottom: 60px;
        }

        @media (max-width: 991px) {
            font-size: 30px;
            margin-bottom: 50px;
        }

        @media (max-width: 600px) {
            font-size: 25px;
            margin-bottom: 30px;
        }
    }

    &-drivers {
        padding: 167px 0 150px;

        @media (max-width: 1699px) {
            padding: 177px 0 122px;
        }

        @media (max-width: 1199px) {
            padding: 140px 0 96px;
        }

        @media (max-width: 991px) {
            padding: 140px 0 96px;
        }

        @media (max-width: 600px) {
            padding: 58px 0 60px;
        }
    }
}
