.s-drivers {
    position: relative;
    padding-top: 35px;
    overflow: visible;

    @media (max-width: 991px) {
        padding-top: 10px;
    }

    @media (max-width: 600px) {
        padding-top: 48px;
    }

    &__text {
        font-size: 25px;
        line-height: 120%;
        color: #4E5056;
        max-width: 467px;
        margin-bottom: 81px;

        @media (max-width: 1199px) {
            max-width: 549px;
            font-size: 19px;
            margin-bottom: 46px;
        }

        @media (max-width: 991px) {
            font-size: 15px;
            max-width: 266px;
            margin-bottom: 34px;
        }

        @media (max-width: 600px) {
            max-width: 100%;
            font-size: 13px;
            margin-bottom: 42px;
        }
    }

    &__caption {
        position: absolute;
        top: -250px;
        right: -12%;
        font-size: 320px;
        font-weight: 900;
        letter-spacing: -0.03em;
        -webkit-text-fill-color: transparent;
        -webkit-text-stroke: 0.25rem rgba(255, 255, 255, 0.05);
        letter-spacing: -0.03em;

        @media (max-width: 1699px) {
            right: -30%;
        }

        @media (max-width: 1199px) {
            top: -100px;
            right: -20%;
            font-size: 170px;
        }

        @media (max-width: 991px) {
            right: -30%;
            font-size: 130px;
        }

        @media (max-width: 600px) {
            right: unset;
            left: 0;
            top: -140px;
        }
    }

    &__content {
        max-width: 1207px;
        margin: 0 auto;

        @media (max-width: 1199px) {
            max-width: 651px;
        }

        @media (max-width: 991px) {
            max-width: 513px;
        }
    }

    &__search {
        position: relative;
        display: flex;
        max-width: 1150px;
        justify-content: space-between;
        margin-bottom: 80px;

        @media (max-width: 1199px) {
            margin-bottom: 60px;
        }

        @media (max-width: 991px) {
            margin-bottom: 50px;
        }

        @media (max-width: 600px) {
            flex-direction: column;
        }

        &-input {
            position: relative;
            flex: 1;
            min-height: 40px;
            margin-right: 30px;

            @media (max-width: 991px) {
                margin-right: 15px;
            }

            @media (max-width: 600px) {
                margin-bottom: 9px;
                margin-right: 0;
            }

            input {
                width: 100%;
                min-height: 100%;
                border-radius: 10px;
                color: #fff;
                border: 1px solid #000203;
                background: none;
                padding-left: 67px;

                @media (max-width: 1199px) {
                    padding-left: 60px;
                }

                @media (max-width: 991px) {
                    padding-left: 55px;
                }

                @media (max-width: 600px) {
                    height: 40px;
                    padding-left: 50px;
                }
            }
        }

        .btn {
            font-size: 19px;
            line-height: 1;
            color: #fff;
            background: #000203;
            border-radius: 10px;
            padding: 9px 30px;

            @media (max-width: 1199px) {
                font-size: 17px;
            }

            @media (max-width: 600px) {
                max-width: 100%;
            }
        }

        &-icon {
            position: absolute;
            top: 50%;
            left: 25px;
            z-index: 1;
            transform: translateY(-50%);

            @media (max-width: 991px) {
                top: 55%;
                left: 15px;
            }

            svg {
                fill: #4E5056;
            }
        }
    }

    &__tabs {
        display: flex;
        margin-bottom: 51px;

        @media (max-width: 1199px) {
            margin-bottom: 49px;
        }

        @media (max-width: 991px) {
            margin-bottom: 26px;
        }

        @media (max-width: 600px) {
            flex-direction: column;
            margin-bottom: 60px;
        }
    }

    &__tab {
        display: flex;
        flex-direction: column;
        min-height: 122px;
        cursor: pointer;
        margin-right: 175px;

        @media (max-width: 1199px) {
            min-height: 103px;
            margin-right: 68px;
        }

        @media (max-width: 991px) {
            min-height: 83px;
            margin-right: 40px;
        }

        @media (max-width: 600px) {
            position: relative;
            flex-direction: row;
            min-height: 0;
            margin-right: 0;
            margin-bottom: 20px;
            padding-left: 30px;
        }

        &:after {
            content: '';
            display: none;
            width: 3px;
            height: 19px;
            background: $red;
            margin-top: auto;

            @media (max-width: 600px) {
                position: absolute;
                top: 0;
                left: 0;
                margin-top: 0;
            }
        }

        &-text {
            @include tr('all');
            position: relative;
            display: flex;
            flex-direction: column;
            font-size: 40px;
            line-height: 1;
            color: #4E5056;
            padding-bottom: 20px;

            @media (max-width: 1199px) {
                font-size: 25px;
                padding-bottom: 18px;
            }

            @media (max-width: 991px) {
                font-size: 19px;
            }

            @media (max-width: 600px) {
                font-size: 12px;
                padding-bottom: 8px;
            }

            &:after {
                position: absolute;
                bottom: 0;
                left: 0;
                content: '';
                display: none;
                width: 100%;
                max-width: 276px;
                height: 2px;
                background: $red;

                @media (max-width: 991px) {
                    max-width: 130px;
                }
            }
        }

        &.active {
            .s-drivers {
                &__tab {
                    &-text {
                        color: #CECED0;

                        &:after {
                            display: block;
                        }
                    }
                }
            }

            &:after {
                display: block;
            }
        }

        &:hover {
            .s-drivers {
                &__tab {
                    &-text {
                        color: #CECED0;
                    }
                }
            }
        }
    }

    &__table {
        display: none;
        max-height: 1151px;
        overflow-y: auto;
        animation: show .4s ease;

        @media (max-width: 1199px) {
            max-height: 1100px;
        }

        @media (max-width: 991px) {
            max-height: 660px;
        }

        @media (max-width: 600px) {
            max-height: 535px;
        }

        &.active {
            display: block;
        }

        &-item {
            display: flex;
            justify-content: space-between;
            margin-bottom: 37px;
            padding-right: 40px;

            @media (max-width: 1199px) {
                margin-bottom: 49px;
            }

            @media (max-width: 991px) {
                margin-bottom: 22px;
            }

            @media (max-width: 600px) {
                margin-bottom: 17px;
                padding-right: 20px;
            }
        }

        &-text {
            font-size: 21px;
            line-height: 1;
            color: #9A9EA9;

            @media (max-width: 1199px) {
                font-size: 17px;
            }

            @media (max-width: 991px) {
                font-size: 13px;
            }
        }

        &-right {
            display: flex;
            align-items: center;

            &:hover {
                svg {
                    fill: $red;
                }
            }
        }

        &-icon {
            font-size: 40px;
            line-height: 120%;
            margin-right: 30px;

            @media (max-width: 1199px) {
                font-size: 32px;
                margin-right: 26px;
            }

            @media (max-width: 991px) {
                font-size: 20px;
                margin-right: 20px;
            }

            @media (max-width: 600px) {
                margin-right: 9px;
            }

            svg {
                @include tr('all');
                fill: #EBEBEF;
            }
        }

        &-weight {
            font-size: 21px;
            line-height: 120%;
            color: #333333;

            @media (max-width: 1199px) {
                font-size: 17px;
            }

            @media (max-width: 991px) {
                font-size: 13px;
            }

            @media (max-width: 600px) {
                font-size: 12px;
            }
        }
    }
}
