.s-top-menu {
    z-index: 1;
    position: relative;
    margin: rem(20) 0;
    padding: rem(8) 0;
    background: rgba(0, 0, 0, 0.4);

    @media (min-width: 768px) {
        margin: rem(35) 0;
        padding: rem(19) 0;
    }

    @media (min-width: 1024px) {
        margin: rem(30) 0;
        padding: rem(22) 0;
    }

    @media (min-width: 1600px) {
        margin: rem(40) 0;
        padding: rem(26) 0;
    }

    &__title {
        margin-bottom: rem(60);
    }

    &__item {
        overflow: hidden;
        cursor: pointer;
        position: relative;
        display: block;
        height: rem(103);
        border-radius: rem(5);

        @media (min-width: 768px) {
            height: rem(136);
        }

        @media (min-width: 1024px) {
            height: rem(161);
        }

        @media (min-width: 1600px) {
            height: rem(187);
        }

        &-img {
            overflow: hidden;
            width: 100%;
            height: 100%;
            min-height: rem(187);

            @media screen and (max-width: 600px) {
                min-height: rem(103);
            }

            img {
                position: absolute;
                width: 100%;
                height: 100%;
                border-radius: rem(5);
                object-fit: cover;
            }
        }

        &-info {
            z-index: 2;
            position: absolute;
            bottom: rem(10);
            left: rem(6);
            color: $grey_light_more_2;

            @media (min-width: 768px) {
                bottom: rem(14);
                left: rem(8);
            }

            @media (min-width: 1024px) {
                bottom: rem(17);
                left: rem(10);
            }

            @media (min-width: 1600px) {
                bottom: rem(23);
                left: rem(10);
            }
        }

        &-title {
            z-index: 1;
            position: relative;
            font-size: rem(13);
            font-weight: 700;
            line-height: 1.2;

            @media (min-width: 768px) {
                font-size: rem(15);
            }

            @media (min-width: 1024px) {
                font-size: rem(19);
            }
        }

        &-text {
            z-index: 1;
            position: relative;
            font-size: rem(10);
            line-height: rem(12);
        }
    }

    &__slide {
        width: rem(146);
        height: auto;
        transition: 0.5s;

        @media (min-width: 768px) {
            width: rem(193);
        }

        @media (min-width: 1024px) {
            width: rem(228);
        }

        @media (min-width: 1600px) {
            width: rem(265);
        }

        &:after {
            z-index: -10;
            position: absolute;
            top: 50%;
            left: 50%;
            width: calc(100% + 5%);
            height: calc(100% + 5%);
            border: rem(2) solid #c6c7d000;
            border-radius: rem(10);
            transform: translate(-50%, -50%);
            transition: 0.5s;
            filter: blur(4.5px);
            content: "";
        }

        &:hover {
            &:after {
                border: rem(2) solid #c6c7d0;
                content: "";
            }
        }
    }

    .is-active {
        &:after {
            border: rem(2) solid #c6c7d0;
            filter: blur(4.5px);
            content: "";
        }
    }

    &__slider {
        max-width: rem(1040);
        margin: 0 auto;
    }

    .swiper-container {
        overflow: visible;
    }
}
