@import "helpers/index";
@import "varibles/index";
@import "../blocks/modules/support/s_top_menu/s_top_menu.scss";
@import "../blocks/modules/legal/s_support_form/s_support_form";
@import "../blocks/modules/s_drivers/s_drivers";

.s-banner {
    overflow: hidden;

    &__title {
        max-width: rem(546);
        margin-bottom: rem(89);
        color: #9a9ea9;
        -webkit-text-fill-color: #9a9ea9;

        @media (min-width: 768px) {
            margin-bottom: rem(40);
        }

        @media (min-width: 1024px) {
            margin-bottom: rem(50);
        }

        @media (min-width: 1600px) {
            margin-bottom: rem(100);
        }

        @media (min-width: 1920px) {
            margin-bottom: rem(90);
        }
    }
}

.drivers-page {
    position: relative;

    .main {
        position: relative;

        &:after {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: url('.././img/s-search/searching-result-bg.png');
            content: '';
            background-repeat: no-repeat;
            background-size: cover;
            background-position: top;
        }
    }
}
